import Vue from "vue";
import Router from "vue-router";
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/", // 收益
      component: () => import("@/view/user/detailed.vue"),
      meta: {
        title: '收益明细'
      }
    },
    {
      path: "/GoldCoin", // 金币
      component: () => import("@/view/user/GoldCoin.vue"),
      meta: {
        title: '星币明细'
      }
    },
    {
      path: "/market", // 市场
      component: () => import("@/view/market/index.vue"),
      meta: {
        title: '市场'
      }
    },
    {
      path: "/earnestMoney", // 保证金
      component: () => import("@/view/earnestMoney/index.vue"),
      meta: {
        title: '保证金'
      }
    },
    {
      path: "*",
      redirect: "/",
    }
  ],
  mode: 'hash',
  base: "/demo/web/",
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  }
});
